import { ChangeDetectionStrategy, Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ClipboardModule } from '@angular/cdk/clipboard';

import { FxchButtonComponent } from '@core/controls/fxch-button/fxch-button.component';
import { FxchIconComponent } from '@core/controls/fxch-icon/fxch-icon.component';
import { FxchLogoComponent } from '@app/core/controls/fxch-logo/fxch-logo.component';

@Component({
    selector: 'footer',
    standalone: true,
    imports: [
        CommonModule,
        RouterModule,
        TranslateModule,
        ClipboardModule,
        FxchLogoComponent,
        FxchButtonComponent,
        FxchIconComponent,
    ],
    templateUrl: './footer.component.html',
    styleUrl: './footer.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FooterComponent {

    private static readonly LabelActiveClass = 'fxch-footer-contacts-label--active';
    private static readonly LabelActiveClassVisibleTimeMs = 2000;

    public currentYear: number = new Date().getFullYear();

    public copied(isSuccesfull: boolean, labelElement: HTMLDivElement, value?: string): void {
        if (!value) {
            return;
        }

        if (!isSuccesfull) {
            console.log("Not Copied");
            return;
        }

        labelElement.classList.add(FooterComponent.LabelActiveClass);

        setTimeout(
            () => labelElement.classList.remove(FooterComponent.LabelActiveClass),
            FooterComponent.LabelActiveClassVisibleTimeMs
        );
    }
}
